
.layerC-content-service {
  min-height: 134px;
}
.scene-header {
  padding: 14px 0 0 24px;
}
.scene-header .scene-header-create {
  height: 28px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 28px;
}
.scene-header .scene-header-create .scene-number {
  width: 150px;
}
.scene-header .scene-header-create .scene-create {
  width: 86px;
  height: 28px;
  background: #86b3e7;
  border-radius: 6px;
  font-family: PingFangSC-Semibold;
  font-size: 11px;
  color: #ffffff;
  letter-spacing: -0.23px;
  text-align: center;
  cursor: pointer;
  margin-left: 40px;
}
.scene-header .scene-header-create .scene-create a {
  color: #ffffff;
}
.theSceneList {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  /* 这里需要修改 已存在的场景列表需要响应式布局 未实现 */
}
.theSceneList .sceneList-li {
  background: #ffffff;
  -webkit-box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.05);
          box-shadow: 0 6px 12px 0 rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  min-height: 150px;
  width: 240px;
  margin: 0 24px 24px 0;
}
.theSceneList .sceneList-li .li-box {
  padding: 0 24px;
}
.theSceneList .sceneList-li .li-box .li-box-name {
  min-height: 48px;
  padding: 16px 0 0;
  font-weight: 700;
}
.theSceneList .sceneList-li .li-box .li-box-switch {
  height: 42px;
  padding: 16px 0 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.theSceneList .sceneList-li .li-box .li-box-switch .the-switch-input {
  width: 30px;
  height: 10px;
}
.theSceneList .sceneList-li .li-box .li-box-switch .the-switch-input .el-switch__core {
  width: 30px;
  height: 10px;
}
.theSceneList .sceneList-li .li-box .li-box-switch .the-switch-input .el-switch__core:after {
  top: -4px;
  background: #ffffff;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
}
.theSceneList .sceneList-li .li-box .li-box-btn {
  height: 60px;
  padding: 16px 0 0;
}
.theSceneList .sceneList-li .li-box .li-box-btn .theBTN-turn {
  float: left;
  width: 118px;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
  margin-right: 8px;
  background: #c1e080;
}
.theSceneList .sceneList-li .li-box .li-box-btn .theBTN {
  float: left;
  width: 24px;
  height: 24px;
  font-size: 24px;
  margin-right: 6px;
  cursor: pointer;
}



