
.layerC-content-guest {
  min-height: 134px;
}
.room-guest {
  background: #fff;
}
.sugState {
  padding: 0 12px;
  width: 20%;
}
.sugState .sugState-input {
  padding: 0 0 0 14px;
  width: 65%;
}
.sugState input::-webkit-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.sugState input:-moz-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.sugState input:-ms-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.sugVal {
  padding: 0 12px;
  width: 70%;
}
.sugBTN {
  width: 10%;
  padding: 0 14px 0 12px;
}
.sugBTN .select-li-confirm {
  display: block;
  height: 28px;
  line-height: 28px;
}
.auth-msgs-table {
  padding: 0 12px 30px;
}
.auth-msgs-table .el-table .cell {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
}
.auth-msgs-table .tableBtn {
  float: left;
  width: 24px;
  height: 24px;
  font-size: 24px;
  margin-right: 8px;
  cursor: pointer;
}
.auth-msgs-table .el-table--enable-row-hover .el-table__body tr:hover {
  background: #f4f4f4 !important;
  border-radius: 8px !important;
}
.auth-msgs-table .el-table td,
.auth-msgs-table .el-table th.is-leaf {
  border: 0 none;
}



