
.layerC-content-roomarea {
  min-height: 134px;
}
.room-roomarea {
  background: #fff;
}
.authorVal {
  width: 86%;
  padding: 0 12px 0 10px;
}
.authorBTN {
  width: 12%;
  padding: 0 12px 0 10px;
}
.authorBTN .select-li-confirm {
  display: block;
  height: 28px;
  line-height: 28px;
}
.auth-msgs-table {
  padding: 0 12px 30px;
}
.auth-msgs-table .el-table .cell {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
}
.auth-msgs-table .tableBtn {
  float: left;
  width: 24px;
  height: 24px;
  font-size: 24px;
  margin-right: 8px;
  cursor: pointer;
}
.auth-msgs-table .el-table--enable-row-hover .el-table__body tr:hover {
  background: #f4f4f4 !important;
  border-radius: 8px !important;
}
.auth-msgs-table .el-table td,
.auth-msgs-table .el-table th.is-leaf {
  border: 0 none;
}



