}
.organ-btn-menu .organ-btn {
  float: left;
  width: 64px;
  height: 24px;
  margin: 0 8px 0 0;
  border-radius: 6px;
  line-height: 24px;
  text-align: center;
  font-family: PingFangSC-Semibold;
  font-size: 12px;
  color: #ffffff;
  letter-spacing: -0.23px;
  cursor: pointer;
}
.organ-btn-menu .organ-btn.add {
  background: #c1e080;
}
.organ-btn-menu .organ-btn.edit {
