
.layerC-content-service {
  min-height: 134px;
}
.servState {
  padding: 0 12px;
  width: 20%;
}
.servState .servState-input {
  padding: 0 0 0 14px;
  width: 65%;
}
.servState input::-webkit-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.servState input:-moz-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.servState input:-ms-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.servVal {
  padding: 0 12px;
  width: 70%;
}
.servBTN {
  width: 10%;
  padding: 0 14px 0 12px;
}
.servBTN .select-li-confirm {
  display: block;
  height: 28px;
  line-height: 28px;
}
.room-service {
  background: #fff;
}
.auth-msgs-table {
  padding: 0 12px 30px;
}
.auth-msgs-table .el-table .cell {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
}
.auth-msgs-table .el-table--enable-row-hover .el-table__body tr:hover {
  background: #f4f4f4 !important;
  border-radius: 8px !important;
}
.auth-msgs-table .el-table td,
.auth-msgs-table .el-table th.is-leaf {
  border: 0 none;
}



