
.layerC-content-user {
  min-height: 134px;
}
.room-user {
  background: #fff;
}
.user-msgs-table {
  padding: 0 12px 18px;
  height: 300px;
  overflow: auto;
}
.user-msgs-table .el-table {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
}
.user-msgs-table .el-table .el-table__header th.is-leaf {
  border: 0 none;
}
.user-msgs-table .el-table .el-table__header thead {
  color: #303030;
  letter-spacing: -0.25px;
  font-weight: 500;
}
.user-msgs-table .el-table .el-table__header th {
  font-weight: 500;
}
.user-msgs-table .el-table .el-table__body tbody tr:nth-child(odd) {
  background: #fefefe;
}
.user-msgs-table .el-table .el-table__body tbody tr:nth-child(even) {
  background: #fafafa;
}
.user-msgs-table .el-table:before {
  height: 0;
}
.user-msgs-table .el-table--enable-row-hover .el-table__body tr:hover > td {
  background-color: #eee !important;
}
.user-msgs-table .tableBtn {
  float: left;
  width: 24px;
  height: 24px;
  font-size: 24px;
  margin-right: 8px;
  cursor: pointer;
}
.user-msgs-table .tableTag {
  margin-bottom: 2px;
}
.user-table-bottom {
  height: 60px;
  padding: 12px 12px 24px 12px;
  margin-top: 20px;
}
.user-table-bottom .add-user,
.user-table-bottom .talbe-paging {
  height: 24px;
}
.user-table-bottom .add-user .add-user-btn {
  height: 24px;
  font-family: PingFangSC-Semibold;
  font-size: 12px;
  color: #ffffff;
  background: #86b3e7;
  letter-spacing: -0.23px;
  text-align: center;
  padding: 0 12px;
  line-height: 24px;
  border-radius: 6px;
}
.create-user-form .el-dialog .el-dialog__header .el-dialog__title {
  font-family: PingFangSC-Semibold;
  font-size: 14px;
  color: #303030;
  letter-spacing: -0.25px;
  text-align: center;
  font-weight: bolder;
}
.create-user-form .form-list .form-list-li {
  margin-bottom: 20px;
  height: 28px;
}
.create-user-form .form-list .form-list-li .name,
.create-user-form .form-list .form-list-li .msg {
  float: left;
}
.create-user-form .form-list .form-list-li .name {
  height: 28px;
  text-align: right;
  line-height: 28px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #303030;
  letter-spacing: -0.25px;
}
.create-user-form .form-list .form-list-li .msg {
  padding-left: 16px;
  height: 28px;
}
.create-user-form .form-list .form-list-li .msg .el-autocomplete {
  width: 100%;
}
.create-user-form .form-list .form-list-li .msg .el-select {
  width: 100%;
}
.create-user-form .form-list .form-list-li .msg .msg-input .el-input__inner {
  height: 28px;
  border: 0 none;
  background: #f4f4f4;
  border-radius: 6px;
}
.create-user-form .form-list .form-list-li .msg .msg-input .el-input__suffix {
  height: 28px;
}
.create-user-form .form-list .form-list-li .msg .msg-input .el-input__suffix .el-input__icon {
  line-height: 28px;
}



