
.layerC-content-abnor {
  min-height: 134px;
  max-height: 570px;
  overflow: auto;
}
.room-abnor {
  background: #fff;
}
.abType {
  width: 12%;
  padding: 0 10px;
}
.abState {
  width: 10%;
  padding: 0 10px;
}
.abType input::-webkit-input-placeholder,
.abState input::-webkit-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.abType input:-moz-placeholder,
.abState input:-moz-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.abType input:-ms-input-placeholder,
.abState input:-ms-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.abVal {
  padding: 0 10px;
  width: 65%;
}
.abVal .el-input {
  height: 28px;
}
.abVal .el-input .el-input__inner {
  height: 28px;
  background: #f4f4f4;
  border-radius: 6px;
  border: 0 none;
}
.abBTN {
  width: 12%;
  padding: 0 14px;
}
.abBTN .select-li-confirm {
  display: block;
  height: 28px;
  line-height: 28px;
}
.abnormal-table {
  padding: 0 12px 30px;
}
.abnormal-table .el-table .cell {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
}
.abnormal-table .el-table--enable-row-hover .el-table__body tr:hover {
  background: #f4f4f4 !important;
  border-radius: 8px !important;
}
.abnormal-table .el-table td,
.abnormal-table .el-table th.is-leaf {
  border: 0 none;
}
.abnormal-table .tableBtn {
  float: left;
  width: 24px;
  height: 24px;
  font-size: 24px;
  margin-right: 8px;
  cursor: pointer;
}
.abnormal-table .tableTag {
  margin-bottom: 2px;
}
.abnormal-table .element-table-test .el-checkbox__input.is-checked .el-checkbox__inner,
.abnormal-table .element-table-test .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #86b3e7;
  border-color: #86b3e7;
}
.abnormal-table .element-table-test .el-checkbox__inner:hover {
  border-color: #86b3e7;
}
.abnormal-table .element-table-test .el-checkbox .el-checkbox__inner {
  border-radius: 50%;
}



