/* init start */
div,
p,
ul,
li,
ol,
dl,
dd,
dt,
span,
p,
table,
thead,
tbody,
td,
th,
tr,
em,
i,
strong,
b,
img,
button,
form,
select,
option,
input,
a,
label,
nav,
section {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
  border: 0 none;
}
* {
  padding-left: 0;
  padding-right: 0;
}
.clearfix {
  zoom: 1;
}
html,
body {
  height: 100%;
  width: 100%;
  background: #f4f4f4;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
a {
  color: #606060;
  text-decoration: none;
}
a:hover {
  color: #606060;
  text-decoration: none;
}
input {
  outline: 0 none;
  border: 0 none;
}
button {
  outline: 0 none;
  border: 0 none;
}
.clearfix:after {
  content: " ";
  clear: both;
  font-size: 0;
  line-height: 0;
  height: 0;
  display: block;
  visibility: hidden;
}
ul,
ol,
li {
  list-style: none;
  margin: 0;
}
img {
  vertical-align: middle;
}
em {
  font-style: normal;
}
b,
strong {
  font-weight: 500;
}
/* 字体图标颜色 */
.iconColor30 {
