
.layerC-content-log {
  min-height: 134px;
}
.room-log {
  background: #fff;
}
.layerC-bottom-host {
  height: 78px;
  padding: 24px;
}
.layerC-bottom-host .create-btn {
  float: left;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  margin-right: 8px;
}
.layerC-bottom-host .newAbnormal {
  background: #c1e080;
}
.layerC-bottom-host .selectDelete {
  background: #ffa265;
}
.sugState {
  padding: 0 12px;
  width: 20%;
}
.sugState .sugState-input {
  padding: 0 0 0 14px;
  width: 65%;
}
.sugState input::-webkit-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.sugState input:-moz-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.sugState input:-ms-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
  text-align: center;
}
.sugVal {
  padding: 0 12px;
  width: 70%;
}
.sugBTN {
  width: 10%;
  padding: 0 14px 0 12px;
}
.sugBTN .select-li-confirm {
  display: block;
  height: 28px;
  line-height: 28px;
}
.suggest-table {
  padding: 0 12px 30px;
}
.suggest-table .el-table .cell {
  font-family: PingFangSC-Regular;
  font-size: 11px;
  color: #303030;
  letter-spacing: -0.23px;
}
.suggest-table .el-table--enable-row-hover .el-table__body tr:hover {
  background: #f4f4f4 !important;
  border-radius: 8px !important;
}
.suggest-table .el-table td,
.suggest-table .el-table th.is-leaf {
  border: 0 none;
}
.suggest-table .tableBtn {
  float: left;
  width: 24px;
  height: 24px;
  font-size: 24px;
  margin-right: 8px;
  cursor: pointer;
}
.suggest-table .tableTag {
  margin-bottom: 2px;
}
.suggest-table .element-table-test .el-checkbox__input.is-checked .el-checkbox__inner,
.suggest-table .element-table-test .el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #86b3e7;
  border-color: #86b3e7;
}
.suggest-table .element-table-test .el-checkbox__inner:hover {
  border-color: #86b3e7;
}
.suggest-table .element-table-test .el-checkbox .el-checkbox__inner {
  border-radius: 50%;
}



